        <span light v-tooltip:bottom="{ html: '★★★★' }" primary>{{P5}},</span>
        <span light v-tooltip:bottom="{ html: '★★★★★' }" primary>{{CSS3}},</span>
        <span light v-tooltip:bottom="{ html: '★★★★★' }" primary>{{HTML5}}</span>
