        </a>
        <a href="https://www.linkedin.com/in/dawid-nawrocki-1a333913b/" target="_blank">
          <i class="icon-linkedin-squared"></i>
        </a>
        <a href="https://codepen.io/dawiddeveloperDe/" target="_blank">
          <i class="icon-codepen"></i>
        </a>
        <a href="https://join.slack.com/dev-info-team/shared_invite/MjAwMjQwNTA0NzQyLTE0OTc3MzczODktNmJjY2U1NjZkMQ" target="_blank">
          <i class="icon-slack"></i>
        </a>
      </span>
    </div>
  </div>
</template>
<!-- SCRIPT -->
<script>
  export default {
    name: 'hello',
    data() {
      return {
        skills: false,
        msg: 'Dawid Nawrocki',
        link: "Front-end developer",
        JavaScript: "JavaScript",
        Vue: "Vue.js",
        jQuery: "jQuery",
        P5: "P5.js",
        CSS3: "CSS3",
        HTML5: "HTML5",
        items: [{
          icon: 'check_box',
          iconClass: 'grey lighten-1 white--text',
          title: 'HTML5',
          subtitle: 'Clean markup with HTML5 semantic tags'
        }, {
          icon: 'check_box',
          iconClass: 'grey lighten-1 white--text',
          title: 'CSS',
          subtitle: 'RWD or Bootstrap, Vuetify, Foundation etc.'
        }, {
          icon: 'check_box',
          iconClass: 'grey lighten-1 white--text',
          title: 'JavaScript',
          subtitle: '+ AJAX & JSON'
        }, {
          icon: 'check_box',
          iconClass: 'grey lighten-1 white--text',
          title: 'Vue.js | jQuery | P5.js',
          subtitle: 'Modern javascript frameworks and libraries'
        }],
        items2: [{
          icon: 'assignment',
          iconClass: 'blue white--text',
          title: 'Sass / Less',
          subtitle: 'with BEM / SMACKS / OOCSS'
        }, {
          icon: 'call_to_action',
          iconClass: 'amber white--text',
          title: 'Gulp / GIT / JIRA',
          subtitle: '...and more...'
        }, ]
      }
    },
    methods: {
      toggles: function (e) {
        e.preventDefault();
        this.skills = !this.skills;
      }
    }
  }

</script>
<!-- CSS -->
<style scoped>
  .skills-list {
    position: absolute;
    top: -190px;
    right: 60px;
    z-index: 1;
    width: 400px;
    transition: all .3s;
  }

  .hello {
    display: flex;
    justify-content: center;
    transform: translateY(25vh);
  }

  h1 {
    text-align: center;
    color: #fff;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 5em;
    font-weight: 600;
    text-shadow: 0px -1px 0px #000;
