    width: 80vw;
    padding: 5px 0 10px 0;
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 1.5em;
    text-shadow: 0px -1px 0px #000;
    color: #fff;
    text-overflow: ellipsis;
    white-space: nowrap;
