    text-align: center;
  }

  a {
    color: #919191;
  }

  i {
    transition: all .3s;
  }

  i:hover {
    color: #000052;
  }

  .underline {
    text-decoration: underline;
  }

  .social-media {
    text-align: center;
    font-size: 20px;
    color: #fff;
    display: flex;
    padding: 0;
