    justify-content: center;
  }

  .social-media a {
    color: #fff;
  }

  [data-tooltip][data-tooltip-location=bottom]:before {
    top: 35%;
    height: 26px;
    background: transparent;
    box-shadow: 0 0 2px #fff;
  }

  @media screen and (max-width: 600px) {
    .header {
      font-size: 40px;
